
.header {
    padding: 20px;
}

.button {
    width: 130px;
    height: 40px;
    margin: 10px;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
    background: aliceblue;
    transition: 0.2s linear;
}

.button:hover {
    background: lightgrey;
    transition: .2s linear;
}